<template>
  <div class="add-tutorial">
    <el-card class="box-card" v-if="!submitted">
      <h1>ADD TUTORIAL</h1>
      <el-form
        :model="formTutorial"
        ref="formTutorial"
        :label-position="labelPosition"
        label-width="100px"
      >
        <el-form-item label="Title" prop="title">
          <el-input
            type="text"
            v-model="formTutorial.title"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="Description" prop="description">
          <el-input
            type="text"
            v-model="formTutorial.description"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-button @click="saveTutorial" class="primary">Submit</el-button>
      </el-form>
    </el-card>
    <div v-else>
      <h4>You submitted successfully!</h4>
      <el-button class="primary" @click="newTutorial">Add</el-button>
    </div>
  </div>
</template>

<script>
import TutorialDataService from "../../api/tutorial-service";

export default {
  name: "add-tutorial",
  data() {
    return {
      formTutorial: {
        id: null,
        title: "",
        description: "",
        published: false,
      },
      labelPosition: "top",
      submitted: false,
    };
  },
  methods: {
    saveTutorial() {
      var data = {
        title: this.formTutorial.title,
        description: this.formTutorial.description,
      };

      TutorialDataService.create(data)
        .then((response) => {
          this.formTutorial.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
          // this.$router.push({
          //   name: "tutorial",
          //   params: { id: response.data.id },
          // });
          this.$router.push({
            name: "tutorials",
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    newTutorial() {
      this.submitted = false;
      this.formTutorial = {};
    },
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
.add-tutorial {
  width: 600px;
  margin: 0 auto;
  /* display: flex;
  justify-content: center; */
}
</style>